import React, { Component } from 'react';
import { Form } from 'antd';
import UpdateMessageForm from './UpdateMessageForm';
import '../stylesheets/UserMessage.css';
import { Get } from '../utils/Request';

export default class UserMessage extends Component{
    constructor(props){
        super(props);
        this.state={
            user : {
                username : this.props['username'] || '张三'
            }
        };
    }
    componentWillMount(){
        let url = `/api/user/home/message?username=${this.state['user'].username}`;
        const res = Get(url);
        let fakeUser = {
            userAvatarSrc : 'a',
                    email : 'a',
                    username : 'a',
                    phone : 'a',
                    address : 'a'
        };
        res.then(response=>{
            let {code, message, data} = response;
            this.setState({
                // user : {
                //     userAvatarSrc : data['用户头像'],
                //     email : data['电子邮箱'],
                //     username : data['用户名'],
                //     phone : data['手机号码'],
                //     address : data['收货地址']
                // }
                user : fakeUser
            });
        });
    }
    render(){
        console.log(this.props['location'])
        const WrappedUpdateForm = Form.create({ name: 'register' })(UpdateMessageForm);
        return(<div id='userMessage'>
            <WrappedUpdateForm user={this.state['user']} />
        </div>);
    }
}